import React from 'react';

const VideoDetail = ({ video }) => {
  // condition to wait for info to be fetched:
  if (!video) {
    return <div>Loading....</div>;
  }
  // get video id to display:
  const videoId = video.id.videoId;
  const url = `https://www.youtube.com/embed/${videoId}`;

  return (
    <div className="col-lg-8 video-detail mb-3  pl-0">
      <div className="card">
        <div className="embed-responsive embed-responsive-16by9">
          <iframe className="embed-responsive-item" src={url} ></iframe>
        </div>
        <div className="details card-body">
          <h4 className="card-title mb-1">{video.snippet.title}</h4>
          <p className="card-subtitle text-muted mb-2">{video.snippet.channelTitle}</p>
          <p className="card-text">{video.snippet.description}</p>
        </div>
      </div>  
    </div>
  );
};

export default VideoDetail;


