import './App.css';
import _ from 'lodash';
import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import SearchBar from './components/search_bar';
import VideoList from './components/video_list';
import VideoDetail from './components/video_detail';
import YTSearch from 'youtube-api-search';
import registerServiceWorker from './registerServiceWorker';

const API_KEY = 'AIzaSyAp_UiSDxSPcllpP-aaYSpG6q_A68pU8Jg';

// 1- create new component. this component should produce html.
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      videos: [],
      selectedVideo: null
    };
    this.videoSearch('Ronaldo');
  }
  videoSearch(term) {
    YTSearch({
      key: API_KEY,
      term: term
    }, (videos) => {
      this.setState({videos: videos, selectedVideo: videos[0]});
    });
  }
  render() {
    const videoSearch = _.debounce((term) => {
      this.videoSearch(term);
    }, 300);
    return (
      <div className="mt-3">
        <h4 className="text-primary text-center">
          <a href="https://www.youtube.com" className="mr-2">
            <span className="yt"></span>
          </a>
          Search App</h4>
        <SearchBar onVideoSearch={videoSearch}/>

        <div className="container-fluid mt-4">
          <div className="row">
            <VideoDetail video={this.state.selectedVideo}/>
            <VideoList
              onVideoSelect={(selectedVideo) => this.setState({selectedVideo})}
              videos={this.state.videos}/>

          </div>
        </div>
      </div>
    );
  }
}
export default App;



